.btn,
.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 1.5rem;
  display: inline-block;
  border-radius: 10rem;
  transition: all .2s;
  position: relative;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn:hover {
  transform: translateY(-0.3rem);
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn:active,
.btn:focus {
  transform: translateY(-0.1rem);
}

.btn--white {
  background-color: #ffffff;
  background-color: var(--color-white);
}

.btn--white::after {
  background-color: #ffffff;
  background-color: var(--color-white);
}

.btn--orange {
  background-color: #D07B2F;
  background-color: var(--color-primary);
  color: #ffffff;
  color: var(--color-white);
  font-size: 2rem;
}

.btn--orange::after {
  background-color: #D07B2F;
  background-color: var(--color-primary);
}

.btn--form {
  background-color: transparent;
  color: #ffffff;
  color: var(--color-white);
  font-size: 2rem;
  overflow: hidden;
}

.btn--form::after,
.btn--form:hover {
  transform: none;
  background-color: #D07B2F;
  background-color: var(--color-primary);
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn-text:link,
.btn-text:visited {
  color: #D07B2F;
  color: var(--color-primary);
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #D07B2F;
  border-bottom: 1px solid var(--color-primary);
  padding: 3px;
  transition: all .2s;
  outline: none;
}

.btn-text:hover {
  background-color: #D07B2F;
  background-color: var(--color-primary);
  color: #ffffff;
  color: var(--color-white);
  box-shadow: 0 1rem 2rem rgba(0,0,0,.15);
  box-shadow: var(--shadow-dark);
  transform: translateY(-2px);
}

.btn-text:active {
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
  box-shadow: var(--shadow-light);
  transform: translateY(0);
}

