  color: #4CAE50 !important;
  color: var(--color-secondary) !important;
}

.ant-tabs-ink-bar {
  background-color: #4CAE50;
  background-color: var(--color-secondary);
}

.btnUp-container {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 25;
}

.btn-up {
  margin: 3rem;
  visibility: hidden;
  background-color: #ffffff;
  background-color: var(--color-white);
  color: #8AC24A;
  color: var(--color-secondary-light);
  border-radius: 50%;
  border-color: #8AC24A;
  border-color: var(--color-secondary-light);
  box-shadow: 0 0.5rem 1rem var(--color-sencodary);
  transition: all .5s ease-in-out;
}

.btn-up-visible {
  visibility: visible;
}

.btn-up:hover,
.btn-up:active,
.btn-up:focus {
  color: #8AC24A;
  color: var(--color-secondary-light);
  border-color: #8AC24A;
  border-color: var(--color-secondary-light);
  transform: translateY(-0.1rem);
}

footer {
